@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.products {
    @include adaptive-value(padding-bottom, 80, 20, 1);
    // .products__header
    &__header {
        display: flex;
        flex-wrap: wrap;
        align-items: baseline;
        justify-content: space-between;
        @include adaptive-value(margin-bottom, 40, 20, 1);
    }
    // .products__title
    &__title {
        font-weight: 700;
        @include adaptive-font(36, 24, 0);
        line-height: 130%;
        color: #070c11;
        @include adaptive-value(margin-right, 30, 20, 1);
    }
    // .products__link
    &__link {
        display: inline-block;
        color: #2a5275;
        position: relative;
        padding-right: 15px;
        &::after {
            position: absolute;
            content: "";
            right: 0;
            bottom: 2px;
            width: 13px;
            height: 8px;
            background-image: url(../../assets/images/icons/triangle.svg);
            background-repeat: no-repeat;
            background-position: center;
        }
        @media (any-hover: hover) {
            &:hover {
                transition: all 0.2s;
                color: #4878a6;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            border-radius: 1px;
            transition: none;
        }
    }
    // .products__row
    &__row {
        display: flex;
        flex-wrap: wrap;
        @include adaptive-value(row-gap, 40, 20, 1);
        @media (max-width: 620px) {
            justify-content: center;
        }
    }
}